body {
  background-color: #f5f5f5;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

h1 {
  text-align: center;
  color: #333;
}

.search-form {
  text-align: center;
  padding: 20px 0;
}

.search-form input[type="text"] {
  width: 80%;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 10px;
  font-size: 16px;
}

.search-form button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
}

.search-form button[type="submit"]:hover {
  background-color: #0056b3;
}

.gallery {
  max-width: 1200px;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 auto;
  padding: 20px;
  display: flex;
}

.photo-card {
  width: calc(33.33% - 20px);
  background-color: #fff;
  margin: 10px 0;
  transition: transform .2s;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.photo-card:hover {
  transform: scale(1.02);
}

.photo-card img {
  max-width: 100%;
  height: auto;
  border-radius: 4px 4px 0 0;
  display: block;
}

.info {
  padding: 10px;
}

.info-item {
  margin: 0;
  font-size: 14px;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  margin: 20px auto;
  padding: 10px 20px;
  display: none;
}

.load-more:hover {
  background-color: #0056b3;
}

.message {
  text-align: center;
  color: #fff;
  background-color: #ff7b7b;
  border-radius: 4px;
  margin: 20px auto;
  padding: 10px;
}

.photo-card img {
  cursor: pointer;
  transition: transform .3s;
}

.photo-card img:hover {
  transform: scale(1.2);
}

.lightbox {
  width: 100%;
  height: 100%;
  z-index: 999;
  text-align: center;
  background-color: rgba(0, 0, 0, .7);
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.close-button {
  cursor: pointer;
  color: #fff;
  font-size: 24px;
  position: absolute;
  top: 10px;
  right: 10px;
}

.lightbox-image {
  max-width: 80%;
  max-height: 80%;
  cursor: pointer;
  margin: auto;
  display: block;
}

